import * as React from 'react';
import type { Plugin } from '@ali/4ever-cangjie';

export interface Plugins {
  array: Plugin[];
  hash: Record<string, Plugin>;
}

export interface PluginsInjectedProps {
  plugins: Plugins;
}

export const PluginsContext = React.createContext<Plugins | null>(null);

export function usePlugins() {
  return React.useContext(PluginsContext);
}

export function connectPlugins<P extends PluginsInjectedProps>(
  Component: React.ComponentType<P>,
) {
  return (props: Omit<P, 'plugins'>) => {
    const plugins = usePlugins();
    // @ts-ignore
    return <Component {...props} plugins={plugins} />;
  };
}
